<template>
  <div class="table-list-page">
    <gs-search
      class="search-table"
      placeholder="请输入业务对象名称"
      @search="handleSearch"
    />
    <div class="content-header">
      <bread-crumb current-level="业务对象" />
    </div>
    <two-column-layout>
      <side-tree
        ref="sideTree"
        slot="left"
        :loading="isLoadingCategory"
        :active="activeCategory"
        :actions="actions"
        :tree-props="treeProps"
        @search="handleCategorySearch"
        @node-click="handleSelectCategory"
        @node-action="handleNodeAction"
        @create-category="editCategory(1)"
        @set-active="setActive"
        @node-toggle="handleNodeToggle"
      />
      <template slot="right">
        <div class="action-group">
          <add-business
            :category-guid="activeCategory.categoryGuid"
            :category-guname="activeCategory.name"
            :disabled="isDisabled || !activeCategory.categoryGuid"
            @create-success="query"
          />
        </div>

        <data-table
          :loading="isBusinessLoading"
          :table-props="tableProps"
          :pagination-props="paginationProps"
          class="business-table"
          @selection-change="handleSelectionChange"
          @page-change="handlePageChange"
          @page-size-change="handlePageSizeChange"
        >
          <el-table-column
            type="selection"
            width="55"
          />
          <el-table-column
            prop="name"
            label="业务对象名称"
            show-overflow-tooltip
          >
            <template slot-scope="scope">
              <a
                title="查看业务对象名称详情"
                href="javascript:void(0);"
                @click="toDetail(scope.row.businessId)"
              >
                {{ scope.row.name }}
              </a>
            </template>
          </el-table-column>
          <el-table-column
            prop="level2Category"
            label="所属二级部门"
            show-overflow-tooltip
          >
            <template slot-scope="scope">
              {{ scope.row.level2Category || '-' }}
            </template>
          </el-table-column>
          <el-table-column
            prop="path"
            label="路径"
            show-overflow-tooltip
          />
          <el-table-column
            prop="businessStatus"
            label="业务信息状态"
          >
            <template slot-scope="scope">
              <span :class="{'status-text': true, 'status-finished': scope.row.businessStatus === '1'}">
                {{ scope.row.businessStatus === '1' ? '已补充' : '待补充' }}
              </span>
            </template>
          </el-table-column>
          <el-table-column
            prop="technicalStatus"
            label="技术信息状态"
          >
            <template slot-scope="scope">
              <span :class="{'status-text': true, 'status-finished': scope.row.technicalStatus === '1'}">
                {{ scope.row.technicalStatus === '1' ? '已补充' : '待补充' }}
              </span>
            </template>
          </el-table-column>
          <el-table-column
            label="操作"
          >
            <template slot-scope="scope">
              <gs-button
                type="text-primary"
                @click="toDetail(scope.row.businessId)"
              >详情</gs-button>
              <action-comfirm
                v-if="!isDisabled"
                title="确定删除吗？"
                @comfirm="handleDelBusiness(scope.row)"
              >
                <gs-button
                  slot="reference"
                  type="text-primary"
                >
                  {{ singleDeleting === scope.row ? '正在删除...' : '删除' }}
                </gs-button>
              </action-comfirm>
              <gs-button
                v-else
                type="text-primary"
                disabled
              >删除</gs-button>
            </template>
          </el-table-column>
        </data-table>
      </template>
    </two-column-layout>
    <edit-category
      :is-show.sync="visible"
      :type="actionType"
      :category="editingCategory"
    />
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import categoryMixin from './category-mixin';
import DataTable from 'src/components/data-table';
import TwoColumnLayout from 'src/components/two-column-layout';
import ActionComfirm from 'src/components/action-comfirm';
import AddBusiness from './add-business';
import EditCategory from './edit-category';
import BreadCrumb from 'src/components/breadcrumb';
import {
  checkLoading
} from 'src/store/status';
import './style';

import {MODULE} from 'src/utils/index';
const {editBusinessInfo} = MODULE;
// 是否需要显示缓存界面
let keepAlive = false;

export default {
  name: 'BusinessObj',
  components: {
    DataTable,
    TwoColumnLayout,
    ActionComfirm,
    AddBusiness,
    EditCategory,
    BreadCrumb
  },

  mixins: [categoryMixin],

  data() {
    return {
      keyword: '',
      page: 1,
      pageSize: 10,
      selecteds: [],
      singleDeleting: null
    };
  },

  computed: {
    ...mapState('business', {
      tables: 'tables',
      searchTables: 'searchTables',
      tablesStatus: 'tablesStatus'
    }),
    ...mapState('user', {
      modules: 'modules'
    }),

    tableData() {
      const {
        tables,
        searchTables,
        isSearchMode,
        activeCategory
      } = this;
      let data = [];

      if (isSearchMode) {
        data = searchTables;
      } else {
        data = tables;
      }
      // data = tables;

      return (activeCategory.categoryGuid || isSearchMode) ? data : {total: 0, list: []};
    },
    tableProps() {
      const {
        tableData
      } = this;

      return {
        total: tableData.total,
        data: tableData.list
      };
    },
    paginationProps() {
      const total = this.tableProps.total || 0;

      return {
        current: this.page,
        pageSize: this.pageSize,
        total
      };
    },
    isBusinessLoading() {
      return checkLoading(this.tablesStatus);
    },
    isDisabled() {
      const {
        modules
      } = this;
      const hasAuth = modules.modules && modules.modules.some(i => i.moduleId === editBusinessInfo);

      return !hasAuth;
    }
  },

  watch: {
    activeCategory(category) {
      if (category && category.categoryGuid) {
        this.page = 1;
        this.getCategoryBusiness();
      }
    }
  },

  /**
   * 进入页面触发
   */
  beforeRouteEnter(to, from, next) {
    // 获取当前访问是否需要显示缓存页面
    keepAlive = to.meta.keepAlive;
    next();
  },
  /**
   * 路由跳转前触发
   */
  beforeRouteLeave(to, from, next) {
    // 设置不需要缓存
    from.meta.keepAlive = false;
    next();
  },
  /**
   *keep-alive的钩子函数
   */
  activated() {
    // 判断不需要缓存情况下刷新
    if (keepAlive === false) {
      this.keyword = '';
      this.isSearchMode = false;
      this.activeCategory = {};
      this.getTopLevelCategory();
    }
  },
  mounted() {
    this.getTopLevelCategory();
  },

  methods: {
    ...mapActions('business', [
      'getTableList',
      'findTableList',
      'deleteBusiness'
    ]),

    setActive(val) {
      const {
        activeCategory
      } = this;
      if (activeCategory && activeCategory.categoryGuid) return;
      if (val && val.categoryGuid) {
        this.activeCategory = val;
      }
    },

    afterDeleteCategory(categoryGuid, node) {
      // this.getTopLevelCategory();
      const parent = node.$node.parent;
      const children = parent ? parent.data.children : this.categories.list;
      const index = children.findIndex(item => item.categoryGuid === categoryGuid);
      children.splice(index, 1);
      if (this.activeCategory.categoryGuid === categoryGuid) {
        this.activeCategory = {};
      }
      setTimeout(() => {
        // this.getTopLevelCategory();
      }, 50);
    },

    query() {
      if (this.isSearchMode) {
        this.findTableList({
          query: this.keyword,
          offset: (this.page - 1) * this.pageSize,
          limit: this.pageSize
        });
      } else {
        this.getCategoryBusiness();
      }
    },

    getCategoryBusiness() {
      const {
        activeCategory
      } = this;
      if (!activeCategory || !activeCategory.categoryGuid) return;
      return this.getTableList({
        id: activeCategory.categoryGuid,
        param: {
          offset: (this.page - 1) * this.pageSize,
          limit: this.pageSize,
          query: this.keyword
        }
      });
    },

    handlePageChange(page) {
      this.page = page;
      this.query();
    },

    handlePageSizeChange(pageSize) {
      this.page = 1;
      this.pageSize = pageSize;
      this.query();
    },

    handleSearch(value) {
      this.page = 1;
      this.keyword = value;
      this.isSearchMode = !!value;
      this.query();
    },
    /**
     * 关联列表复选框change event
     */
    handleSelectionChange(selecteds) {
      this.selecteds = selecteds;
    },

    toDetail(id) {
      this.$router.push({
        path: `/main/business/detail/${id}`
      });
    },

    handleDelBusiness(row) {
      const businessId = row.businessId;

      if (row) {
        if (this.singleDeleting === row) {
          return;
        }
      }

      let done = () => {
        this.singleDeleting = null;
      };

      if (row) {
        this.singleDeleting = row;
      }

      this.deleteBusiness(businessId).then(() => {
        done();
        this.$Message.success('删除成功');
        this.query();
      }, done);
    },
    handleNodeToggle(node, isToggle) {
    }
  }
};
</script>




